<template>
            <el-select v-model="inputvalue" placeholder="">
            <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
            </el-option>
            </el-select>
            <el-button @click="inputvalue=null"  ></el-button>
            <el-button @click="openWindow" size="small" type="primary"></el-button>
    <el-card>
        <el-table :data="clothingList.filter(data=>!inputvalue||(data.season==inputvalue))" table-layout="fixed" :show-overflow-tooltip="true" 
    :row-style="{height:'20px'}" :cell-style="{padding:'0px'}" style="font-size: 10px"
     fit border >
        
        <el-table-column label="" min-width="50%" >
            <template #default="scope" >
                <el-image style="width: 100%; height: 100%; display: block; margin: 0 auto;" :src="scope.row.url" :zoom-rate="1.2"
            :preview-src-list="scope.row.srcList" :initial-index="4" fit="contain" :preview-teleported="true" />
            </template>
        </el-table-column>
        <el-table-column prop="name" label="" :filters="[{text:'',value:''},{text:'',value:''}]" :filter-method="filterHandler" min-width="40%" />
        <el-table-column prop="body_width" label="" min-width="90%" />
        <el-table-column prop="clothing_length" label="" min-width="50%" />
        <el-table-column prop="shoulder_length" label="" min-width="50%" />
        <el-table-column prop="sleeve_length" label="" min-width="100%" />
        <el-table-column label="" >
            <template #default="scope">
                <el-button style="display:block;margin: 0 auto;" size="small"  @click="handleEdit(scope.$index, true)" ></el-button>
            </template>
        </el-table-column>
        <el-table-column label="" >
            <template #default="scope">
                <el-button style="display:block;margin: 0 auto;" size="small" @click="handleEdit(scope.$index, false)"></el-button>
            </template>
        </el-table-column>
        <el-table-column label="" >
            <template #default="scope">
                <el-button style="display:block;margin: 0 auto;" size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
        </el-table-column>
    
    </el-table>
    </el-card>

    <el-dialog  v-model="windowVisible" :append-to-body="true" width="100%" height="50vh" >
             <EditCoatView v-if="windowVisible" ref="popWindow" :fetchData="fetchData" :inputdisable="inputdisable"></EditCoatView>
              <span slot="footer" class="dialog-footer" v-if="!inputdisable">
                  <el-button type="primary" @click="postform"></el-button>
                  <el-button @click="windowVisible = false"></el-button>
              </span>
    </el-dialog>

</template>
  
<script lang="ts" setup>
import { ref,nextTick,onMounted  } from 'vue';
import EditCoatView from './EditCoatView.vue';
import axios from 'axios';
import { useRoute } from 'vue-router';

const windowVisible = ref(false);
const inputdisable = ref(false);
const popWindow = ref();
const options=[{value: '0', label: ''},
                     {value: '1', label: ''},  
                     {value: '2', label: ''}
                    ];
const inputvalue=ref();


interface Clothing {
    id:Number
    clothingid:Number
    name: string
    url: string
    srcList: string[]
    body_width:string
    clothing_length:string
    shoulder_length:string
    sleeve_length:string
    season:string

}

const clothingList  = ref<Clothing[]>([]);
const tagid= ref<Number>();
var coatlist:any = [];
const fetchData = async (tagid:Number|undefined) => {
    if(!tagid){
        try {
        const headers={'userid':localStorage.getItem('userid')}
        const response = await axios.get('/v1/coat/list',{headers:headers});
        const responseData = response.data;
        coatlist=responseData
      } catch (error) {
        console.error(error);
      }
    }else{
        try {
        const response = await axios.get('/v1/tags/getclothingfrontag',{params:{tagid:tagid,type:0}});
        const responseData = response.data;
        coatlist=responseData
      } catch (error) {
        console.error(error);
      }
    }
    if(coatlist){
    clothingList.value = coatlist.map((item:any) => ({
          id: item.id,
          clothingid: item.clothing.id,
          name: item.clothing.name,
          url: item.clothing.url,
          srcList: item.clothing.srcList ? JSON.parse(item.clothing.srcList) : [],
          body_width:item.bodyWidth,
          clothing_length:item.clothingLength,
          shoulder_length:item.shoulderWidth,
          sleeve_length:item.sleeveLength,
          season:item.clothing.season
        }));}

    };

onMounted(()=>{
    const route = useRoute();
    tagid.value=Number(route.query.tagid)
    console.log(tagid.value)
    fetchData(tagid.value)
})

const  openWindow= () =>{
        windowVisible.value=true
        inputdisable.value=false
        nextTick(()=>{
        popWindow.value.newInit(0)
    })
      }

const handleEdit = (index: number,edit:boolean) => {
    windowVisible.value=true
    inputdisable.value=edit
    nextTick(()=>{
        popWindow.value.dataInit(coatlist[index])
    })
    
    
}

const handleDelete = (index: number, row: Clothing) => {
    if(confirm('?')){
        const url=`/v1/deleted/${row.clothingid}`
        axios.get(url)
        .then(response=>{
            console.log(response.data)
            fetchData(tagid.value)
        })
        .catch(error=>{
            throw new Error(error.response.data)
        })
    }

}
const  postform = async () => {
    let msg=await popWindow.value.updataform();
    console.log(msg)
    if(msg){
        windowVisible.value=false
    }
 
}

const filterHandler=(value:any, row:any, column:any)=>{
        const property = column['property'];
        return row[property].indexOf(value)>-1;
      }

</script>

<style>
.el-table .cell.el-tooltip{
    padding:0 2px;
}
</style>